import { useEffect, useState } from "react";
import "./timer.scss";

const Timer = ({ state }) => {
  // debugger
  const [deadLine] = useState(state.deadLine);
  //  
  const [[dateDays, dateH, dateM, dateS], setDate] = useState([0, 0, 0, 0]);
  //    
  const [tick, setTick] = useState(false);
  // toggle   
  const [isTimeout, setIsTimeout] = useState(false);
  //   clearInterval
  const [timerId, setTimerID] = useState(0);

  //
  useEffect(() => {
    const diff = (Date.parse(deadLine) - new Date()) / 1000;
    if (diff < 0) {
      setIsTimeout(true);
      return;
    }

    setDate([
      Math.floor(diff / 86400), // 
      Math.floor((diff / 3600) % 24),
      Math.floor((diff / 60) % 60),
      Math.floor(diff % 60),
    ]);
  }, [tick, deadLine]);

  useEffect(() => {
    if (isTimeout) clearInterval(timerId);
  }, [isTimeout, timerId]);

  useEffect(() => {
    const timerID = setInterval(() => {
      setTick(!tick);
    }, 1000);
    setTimerID(timerID);

    return () => clearInterval(timerID);
  }, [tick]);

  return (
    <div className="timer">
      <div>
        <p> {`${dateDays}`}</p>
        <p>{state.time.day}</p>
      </div>
      <span>:</span>
      <div>
        <p>{`${dateH.toString().padStart(2, "0")}`}</p>
        <p>{state.time.hour}</p>
      </div>
      <span>:</span>
      <div>
        <p>{`${dateM.toString().padStart(2, "0")}`}</p>
        <p>{state.time.minute}</p>
      </div>
      <span>:</span>
      <div>
        <p>{`${dateS.toString().padStart(2, "0")}`}</p>
        <p>{state.time.second}</p>
      </div>
    </div>
  );
};

export default Timer;
