import { useCallback, useEffect, useState } from "react";
import AboutUs from "./components/pages/about/About";
import Footer from "./components/pages/footer/Footer";
import HeaderPage from "./components/pages/intro/HeaderPage";
import OrgComitetComponent from "./components/pages/orgComitet/orgComitetComponent";
import RegistrationComponent from "./components/pages/registrationComponent/registrationComponent";
import SessionsComponent from "./components/pages/sessions/SessionsComonent";

const MainPage = (props) => {
  // debugger
  const [sectionPage, setSection] = useState([]);

  const nameId = [
    <HeaderPage state={props.state.homePage} />,
    <AboutUs state={props.state.about} />,
    <SessionsComponent />,
    <RegistrationComponent />,
    <OrgComitetComponent />,
  ];

  const refPage = useCallback((node) => {
    if (node !== null) {
      setSection(sectionPage.push(node));
    }
  }, []);

  useEffect(() => {
    props.arrSectionPage(sectionPage);
  }, []);

  return (
    <>
      {props.nameId.map((i, num) => {
        return (
          <div id={i} key={num} ref={refPage} style={{ height: "100vh" }}>
            {nameId ? nameId[num] : <div></div>}
          </div>
        );
      })}
      <hr />
      <Footer />
    </>
  );
};

export default MainPage;
