import "./stepsComponent.scss";
import { Steps } from "antd";

const StepsComponent = () => {
  return (
    <>
      <div className="treeBlock containerContent">
        <div>
          <h1></h1>
          <Steps
            style={{ marginBottom: "20px" }}
            progressDot
            items={[
              {
                status: "finish",
                title: "April 10",
                description: "Notification of accepted papers",
              },
              {
                status: "finish ",
                title: "May 12",
                description: "Submission Deadline",
              },
              {
                status: "finish ",
                title: "underfined",
                description: "underfined",
              },
            ]}
          />
        </div>
        <div>
          <h1></h1>
          <Steps
            progressDot
            items={[
              {
                status: "finish",
                title: "Early bird registration",
                description: "before 2nd September",
              },
              {
                status: "finish ",
                title: "Regular registration ",
                description: "before 18th October.",
              },
              {
                status: "finish ",
                title: "Late registration",
                description: "after 18th October.",
              },
            ]}
          />
        </div>
      </div>
    </>
  );
};

export default StepsComponent;
