import { useEffect, useState } from "react";

import NavBar from "./components/navBar/NavBar";
import MainPage from "./MainPage";

import "./App.scss";

const App = (props) => {
  const [languages, setLanguages] = useState(true);

  const [sectionPage, setSection] = useState([]);
  const [navLi, setNavLi] = useState([]);

  //* arr Navigation (callback)
  const arrNavLi = (arr) => {
    setNavLi(arr);
  };

  // arr Section (callback)
  const arrSectionPage = (arr) => {
    setSection(arr);
  };

  // intersectionObserver is used
  useEffect(() => {
    if (navLi.length > 0 && sectionPage.length > 0) {
      const observer = new IntersectionObserver(
        (entries) => {
          for (let entry of entries) {
            if (entry.isIntersecting) {
              navLi.forEach((item) => {
                item.classList.remove("active");
                if (entry.target.id === item.getAttribute("href").slice(1)) {
                  item.classList.add("active");
                }
              });
            }
          }
        },

        {
          threshold: 0.4,
        }
      );
      sectionPage.forEach((item) => {
        observer.observe(item);
      });
    }
  }, [navLi, sectionPage]);

  const selectLanguage = () => {
    setLanguages(!languages);
  };

  return (
    <>
      <div className="container">
        <div className="navBar">
          <NavBar
            selectLanguage={selectLanguage}
            arrNavLi={arrNavLi}
            nameId={props.state.nameId}
            state={languages ? props.state.rus.navBar : props.state.eng.navBar}
          />
        </div>
        <main className="mainWallper">
          <MainPage
            arrSectionPage={arrSectionPage}
            nameId={props.state.nameId}
            state={languages ? props.state.rus : props.state.eng}
          />
        </main>
      </div>
    </>
  );
};

export default App;
