import "./footer.scss";

import MapComponent from "../mapComponent/mapComponent";

const Footer = () => {
  return (
    <div id="footer" className="footer containerContent">
      <div className="contactBlock">
        <div className="map">
          <MapComponent />
        </div>
        <div className="footerText">
          <div className="contactText">
            <p>Contact us</p>
            <p>
              <a href="mail@physica.spb.ru">mail@physica.spb.ru</a>
            </p>
          </div>
          <div className="contactAdress">
            <p>Adress</p>
            <p>
              Saint Petersburg, Sputnik Hotel Toreza, 36, <br />
              St. Petersburg, 194021, Russia
            </p>
          </div>
        </div>
      </div>

      <div className="signature">
        <p>Powered by Sausan F.</p>
        <p> 2023, PhysicA SPb/2023. All Rights Reserved</p>
      </div>
    </div>
  );
};

export default Footer;
