import { useCallback, useEffect, useState } from "react";
import { Switch } from "antd";
import "./navBar.scss";

import svgGeo from "../../assets/img/svgGeo.svg";
import logo from "../../assets/img/Logo.png";

const NavBar = ({ state, arrNavLi, ...props }) => {
  // debugger;
  const [navLi, setNavLi] = useState([]);

  const refLi = useCallback((node) => {
    if (node && node !== null) {
      setNavLi(() => {
        navLi.push(node);
      });
    }
    arrNavLi(navLi);
  }, []);

  return (
    <>
      <ul>
        {state.pageName.map((i, num) => (
          <li key={num}>
            <a ref={refLi} href={`#${props.nameId[num]}`}>
              {i}
            </a>
          </li>
        ))}
      </ul>
      <figure style={{ textAlign: "center" }}>
        <img src={logo} alt="" />
        <figcaption>{state.logoName}</figcaption>
      </figure>

      <figure style={{ textAlign: "center" }}>
        <a href="#footer">
          <img src={svgGeo} alt="" style={{ width: "20%" }} />
        </a>
        <figcaption>
          <br />
          <pre>{state.geolocation}</pre>
        </figcaption>
      </figure>

      <Switch
        checkedChildren="RU"
        onChange={() => {
          props.selectLanguage();
        }}
        unCheckedChildren="ENG"
        defaultChecked
      />
    </>
  );
};

export default NavBar;
