import Timer from "./timer/Timer";
import "./headerPage.scss";

const HeaderPage = (props) => {
  // debugger;
  return (
    <section className="hero">
      <div className="hero__content">
        <h2 className="conf">{props.state.title}</h2>
        <h1 className="title">{props.state.logoName}</h1>
      </div>

      <Timer className="timer" state={props.state.timer} />
      <div className="invisib"></div>
      <a href="#Information">
        <div className="scroll"></div>
      </a>
    </section>
  );
};

export default HeaderPage;
