import "./map.scss";
import {
  FullscreenControl,
  Map,
  Placemark,
  TypeSelector,
  YMaps,
  ZoomControl,
} from "@pbe/react-yandex-maps";

const MapComponent = () => {
  return (

      <div id="map">
        <YMaps
        // ! 
          query={{
            // lang: 'en_US',
            lang: 'ru_RU',
            apikey: "b6952e57-7566-4f0e-bb8b-23a5ee7b87a5",
          }}
        >
          <Map
            style={{ width: "500px", height: "400px" }}
            defaultState={{
              center: [60.005392, 30.354253],
              zoom: 15,
            }}
          >
            <Placemark
              defaultGeometry={[60.005392, 30.354253]}
              properties={{
                balloonContentHeader: " ",
                balloonContentBody: "flhtc lfyyjq ujcnbybws",
              }}
              options={{
                preset: "islands#darkGreenDotIcon",
              }}
              modules={["geoObject.addon.balloon"]}
            />
            <FullscreenControl />
            <TypeSelector options={{ float: "right" }} />
            <ZoomControl options={{ float: "right" }} />
          </Map>
        </YMaps>
      </div>
  );
};

export default MapComponent;
